/**
 *  Represents the model structure for Edit User returned from API.
 */
export class UserDetails {
    employeeId: string;
    firstName: string;
    lastName: string;
    toc: string;
    terminationDate: Date;
    status: string;
    emailAddress: string;
    roles: number[];
    customerId: string;
    manualPriceAuthorisation: number;
    operatorId: string;
    languageCode: number;
    defaultTillId: string;
    displayName: string;
    userType: number;
    birthDate: Date;
    handCode: number;
    shiftNlc: string;
    shiftTpv: string;
    storeId: number;
    password: string;
    forcePwdChange: boolean;
}