import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {NewtisuserComponent} from './newtisuser.component';
import { UserDetailsResolverService } from 'src/app/shared/service/user-details-resolver.service';
import { TocsResolverService } from 'src/app/shared/service/tocs-resolver.service';
import { LandingPageResolverService } from 'src/app/shared/service/landing-page-resolver.service';

/**
 * Routes defined for Addition and Updation of TIS users along with resolver services
 */
const routes: Routes = [
  {
    path: '',
    component: NewtisuserComponent,
    resolve: {userDetail : UserDetailsResolverService, tocs: TocsResolverService, landingPage : LandingPageResolverService}
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NewtisuserRoutingModule { }
